  				</view>
  				<view class=" text-center margin-top" style="margin-top: 12px;">
  					<button plain="false" class="text-shadow "
  						style="font-size: 12px;float: right;padding-left: 30px;margin-right: 10px;border: 0px;"
  						@tap="forgotPassword">忘记密码 </button>
  					<button plain="false" class=" text-shadow " @tap="register"
  						style="font-size: 12px;float: right;border: 0px;">注册 </button>
  				</view>
  				<view class=" text-center margin-top">
  					<button type="primary"
  						style="width: 100%;;border-radius: 50px;min-height: 52px;margin-top: 150px;"
  						class="cu-btn bg-blue lg shadow" @tap="onLogin"><text space="emsp"> 确定登录</text>
  					</button>
  				</view>

  				<view class=" text-center " style="margin-top: 30px;    text-align: center;">
  					<label class="radio" style="font-size: 12px;">
  						<radio :checked="radioState" style="transform:scale(0.5)" @click="setRadionState"/>我已阅读并同意相关
  						<button plain="false" class=" text-shadow agreeon-css"
  							@click="toggle('bottom')">《用户协议》</button>

  					</label>
