  		<view>
  			<!-- 普通弹窗 -->
  			<uni-popup ref="popup" background-color="#fff" @change="change">
  				<view class="popup-content" 
				style="height: 600px;overflow-y: auto;"><text
  						class="text">
						【特别提示】 工25（以下简称“本软件”）在此特别提醒您（用户）在注册成为用户之前，请认真阅读本《用户使用协议》（以下简称“协议”），确保您充分理解本协议中各条款。请您审慎阅读并选择接受或不接受本协议。特别是涉及免除或者限制责任的条款、权利许可和信息使用的条款、同意开通和使用特殊单项服务的条款、法律适用和争议解决条款等。其中，免除或者限制责任条款等重要内容将以加粗形式提示您注意，您应重点阅读。如您未满18周岁，请您在法定监护人陪同下仔细阅读并充分理解本协议，并征得法定监护人的同意后下载本软件。除非您接受本协议的全部内容，否则您无权下载、安装、注册、登录、使用“工25”软件。您的注册、登录、使用等行为将视为对本协议的接受，并同意接受本协议各项条款的约束。本协议约定工25与用户之间关于“工25”软件服务（以下简称“服务”）的权利义务。“用户”是指注册、登录、使用本服务的个人。本协议可由工25随时更新，更新后的协议条款须由您重新确认是否接受，一旦您点击确认接受，则新的协议条款即代替原来的协议条款对双方具有法律约束力，如您点击不予接受新的协议条款，服务的部分功能的使用可能会受到限制。 一、账号注册 1、用户在使用本服务前需要注册一个“工25”账号。“工25”账号可使用微信或微信账号以外的其他账号注册，请用户使用尚未与“工25”账号绑定、并绑有实名认证银行卡的微信账号，以及未被工25根据本协议封禁的微信账号注册“工25”账号。工25可以根据用户需求或产品需要对账号注册和绑定的方式进行变更，而无须事先通知用户。2、鉴于“工25”账号可能包含手机绑定注册方式，您同意工25在注册时将使用您提供的手机号码及/或自动提取您的手机号码及自动提取您的手机设备识别码等信息用于注册。3、在用户注册及使用本服务时，工25需要搜集能识别用户身份的个人信息以便工25可以在必要时联系用户，或为用户提供更好的使用体验。工25搜集的信息包括但不限于用户的微信昵称、性别、年龄、地址、所属行业、兴趣爱好、常出没的地方、个人说明；工25同意对这些信息的使用将受限于第三条用户个人隐私信息保护的约束。4、在用户注册及使用本服务时，应承诺以真实身份注册成为工25的用户，并保证所提供的个人身份资料信息真实、完整、有效，依据法律规定和必备条款约定对所提供的信息承担相应的法律责任。5、用户以其真实身份注册成为工25用户后，在需要修改所提供的个人身份资料信息时，工25将为用户提供该项服务。同时，您理解并同意出于安全性和身份识别（如账号或密码找回申诉服务等）的考虑，您可能无法修改注册时提供的初始注册信息及其他验证信息。 二、服务内容 1、本服务的具体内容由工25根据实际情况提供，包括但不限于授权用户通过其账号进行登录、聊天、社交平台分享、选择或者放弃邮寄实物、使用积分消费等。工25可以对其提供的服务予以变更，且工25提供的服务内容可能随时变更；用户将会收到工25关于服务变更的通知。2、工25提供的服务有免费服务与收费服务。用户使用“工25”由每次求职服务完结后收取周薪、日薪、时薪所涉薪酬的5% — 10%比例的佣金，月薪、年薪按七天试用期薪酬比例的5% — 10%收取佣金，具体方式为：用户通过网上银行、支付宝或其他“工25”平台合作的付费途径支付，对于收费服务，工25会在用户使用之前给予用户明确的提示，只有企业用户确认其支付费用并完成了支付行为，支付行为的完成以银行或第三方支付平台生成“支付已完成”的确认通知为准。 三、用户个人隐私信息保护 1、用户在注册账号或使用本服务的过程中，可能需要填写或提交一些必要的信息，如法律法规、规章规范性文件（以下称“法律法规”）规定的需要填写的身份信息。如用户提交的信息不完整或不符合法律法规的规定，则用户可能无法使用本服务或在使用本服务的过程中受到限制。2、个人隐私信息是指涉及用户个人身份或个人隐私的信息，比如，用户真实姓名、身份证号、手机号码、手机设备识别码、IP地址、用户聊天记录。非个人隐私信息是指用户对本服务的操作状态以及使用习惯等明确且客观反映在工25服务器端的基本记录信息、个人隐私信息范围外的其它普通信息，以及用户同意公开的上述隐私信息。3、尊重用户个人隐私信息的私有性是工25的一贯制度，工25将采取技术措施和其他必要措施，确保用户个人隐私信息安全，防止在本服务中收集的用户个人隐私信息泄露、毁损或丢失。在发生前述情形或者工25发现存在发生前述情形的可能时，将及时采取补救措施。4、工25未经用户同意不会向任何第三方提供、公开或共享用户注册资料中的姓名、个人有效身份证件号码、联系方式、家庭住址等个人身份信息，但下列情况除外：(1)工25根据法律法规规定或有权机关的指示提供用户的个人隐私信息；(2)由于用户将其用户密码告知他人或与他人共享注册帐户与密码，由此导致的任何个人信息的泄漏，或其他非因工25原因导致的个人隐私信息的泄露；(3)用户自行向第三方公开其个人隐私信息；(4)用户与工25及工25合作单位及关联方之间就用户个人隐私信息的使用公开达成约定，工25因此向工25合作单位及关联方公开用户个人隐私信息；（5）用户或用户监护人授权工25披露的；（6）工25为了维护自己合法权益而向用户提起诉讼或者仲裁时；（7）应用户监护人的合法要求而提供用户个人身份信息时；(8)任何由于黑客攻击、电脑病毒侵入及其他不可抗力事件导致用户个人隐私信息的泄露。5、用户同意软件可在以下事项中使用用户的个人隐私信息：(1)工25向用户及时发送重要通知，如软件更新、本协议条款的变更；(2)工25内部进行审计、数据分析和研究等，以改进工25的产品、服务和与用户之间的沟通；(3)依本协议约定，工25管理、审查用户信息及进行处理措施；(4) 适用法律法规规定的其他事项。 除上述事项外，如未取得用户事先同意，工25不会将用户个人隐私信息使用于任何其他用途。 6、用户确认，其肖像及声音信息为非个人隐私信息，用户成功注册“工25”账号并授权“工25”app使用前置摄像头及麦克风权限，视为确认授权工25在法律法规允许的范围内提取、公开及使用用户的肖像及声音信息。如用户需要终止向其他用户公开其肖像或声音信息，可随时自行设置为不允许授权摄像头或麦克风。7、为了改善工25的技术和服务，向用户提供更好的服务体验，工25或可会在法律法规允许的范围内自行收集使用或向第三方提供用户的非个人隐私信息。 四、内容规范 1、本条所述内容是指用户使用本服务过程中所制作、上载、复制、发布、传播的任何内容，包括但不限于账号头像、名称、用户说明等注册信息及认证资料，或文字、语音等发送、回复或自动回复消息，以及其他使用账号或本服务所产生的内容。2、用户不得利用“工25”账号或本服务制作、上载、复制、发布、传播如下法律、法规和政策禁止的内容：(1)反对宪法所确定的基本原则的；(2)危害国家安全，泄露国家秘密，颠覆国家政权，破坏国家统一的；(3)损害国家荣誉和利益的；(4)煽动民族仇恨、民族歧视，破坏民族团结的；(5)破坏国家宗教政策，宣扬邪教和封建迷信的；(6)散布谣言，扰乱社会秩序，破坏社会稳定的；(7)散布淫秽、色情、赌博、暴力、凶杀、恐怖或者教唆犯罪的；(8)侮辱或者诽谤他人，侵害他人合法权益的；(9)含有法律、行政法规禁止的其他内容的信息。3、用户不得利用“工25”账号或本服务制作、上载、复制、发布、传播如下干扰“工25”正常运营，以及侵犯其他用户或第三方合法权益的内容：(1)含有任何性或性暗示的；(2)含有辱骂、恐吓、威胁内容的；(3)含有骚扰、垃圾广告、恶意信息、诱骗信息的；(4)涉及他人隐私、个人信息或资料的；(5)侵害他人名誉权、肖像权、知识产权、商业秘密等合法权利的；(6)含有其他干扰本服务正常运营和侵犯其他用户或第三方合法权益内容的信息。一经发现，即刻删除。必要时将对用户账户做封禁处理。 五、使用规则 1、用户在本服务中或通过本服务所传送、发布的任何内容并不反映或代表，也不得被视为反映或代表工25的观点、立场或政策，工25对此不承担任何责任，但法律法规另有规定的除外。2、用户不得利用“工25”账号或本服务进行如下行为：(1)提交、发布虚假信息，或盗用他人头像或资料，冒充、利用他人名义的；(2)强制、诱导其他用户关注、点击链接页面或分享信息的；(3)盗刷游戏币、买卖游戏币、游戏造假、作弊的；(4)散布谣言、虚构事实、攻击官方、隐瞒真相以误导、欺骗他人的；(5)利用技术手段批量建立虚假账号的；(6)利用“工25”账号或本服务从事任何违法犯罪活动的；(7)制作、发布与以上行为相关的方法、工具，或对此类方法、工具进行运营或传播，无论这些行为是否为商业目的；(8)其他违反法律法规规定、侵犯其他用户合法权益、干扰“工25”正常运营或软件未明示授权的行为。3、用户须对利用“工25”账号或本服务传送信息的真实性、合法性、无害性、准确性、有效性等全权负责，与用户所传播的信息相关的任何法律责任由用户根据相关法律法规的规定自行承担。如因此给工25或第三方造成损害的，用户应当依法予以赔偿。4、工25提供的服务中可能包括广告，用户同意在使用过程中显示工25和第三方供应商、合作伙伴提供的广告。除法律法规明确规定外，用户应自行对依该广告信息进行的交易负责，对用户因依该广告信息进行的交易或前述广告商提供的内容而遭受的损失或损害，根据相关法律法规的规定确定责任的承担主体和承担方式。5、用户在使用“工25”服务过程中不得未经工25许可以任何方式录制并向他人传播“工25”内容，包括不限于利用任何第三方软件进行网络传播等。 六、账户管理 1、 用户完成